/* //////////////////////////card */

.main{

  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
}
.card{
  position: relative;
  width: 400px;
  height: 400px;
transition: 0.5s;
background-color: #fff;

box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.face{
  position: absolute;
  top: 0;
  left: 0;
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;
width: 100%;
height: 100%;
}
.face1{
  padding: 30px;
  text-align: center;
}
.face h1{
  padding: 20px;
  text-transform: uppercase;
}
.face1 p{
  margin-top: 10px;
  color: rgb(68, 65, 65);
}

.face2{
background-color: #fff;
   
  color: #fff;
  transition: 0.5s;
}
.card:hover .face2{
height: 80px;
width: 80px;
background-color: white;
border-radius: 50%;
color: black;

background-color: #25aae1;
top:20px;
left: 150px;
}
.card:hover .face2 i{
font-size: 30px;
color:#fff;

}
.face2 i{
  font-size: 120px;
  color: orangered;
}
/* //////////////media query */
@media screen and (max-width: 480px) {
  .card{
   
    width: 300px;
    height: 300px;
    font-size: 12px;

  }
  .card:hover .face2{
    height: 80px;
    width: 80px;
    background-color: white;
    border-radius: 50%;
    color: black;
    
    background-color: #25aae1;
    top:5px;
    left: 100px;
    }
}
